.promo{
  &-grid{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    gap: 40px;
    @media screen and (max-width: 991px) {
      grid-template-columns: minmax(0,1fr);
      gap: 20px;
    }
  }
  &-item{
    display: flex;
    align-items: stretch;
    border-radius: 6px;
    overflow: hidden;
    background: var(--color-card-bg);
    @media screen and (max-width: 640px) {
      flex-direction: column;
    }
    &__img{
      flex-shrink: 0;
      width: 215px;
      display: flex;
      align-items: center;
      justify-content: center;
      @media screen and (max-width: 640px) {
        width: 100%;
        height: 200px;
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__title{

    }
    &__content{
      padding: 30px 40px;
      display: flex;
      flex-direction: column;
      gap: 30px;
      @media screen and (max-width: 991px) {
        padding: 15px 20px;
        gap: 20px;
      }
    }
    &__title{
      font-size: 18px;
      color: var(--color-accent);
      font-weight: 700;
      line-height: 1.2;
    }
    &__text{
      display: flex;
      flex-direction: column;
      gap: 20px;

      font-size: 15px;
      color: #fff;
      line-height: 1.2;
    }
  }
}