.faq {
  &-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &-item {
    &--active &__header{
      border-radius: 6px 6px 0 0;
      color: var(--color-accent);
    }
    &--active &__content{
      display: flex;
    }
    &--active &__icon {
      background-color: #111219;

      &::before {
        transform: rotate(-90deg);
        color: var(--color-accent);
      }
    }

    &__content{
      background: #0e0f14;
      padding: 30px;

      display: none;
      flex-direction: column;
      gap: 20px;

      font-size: 15px;
      color: #b1b4c1;

      border-radius: 0 0 6px 6px;
      @media screen and (max-width: 991px) {
        padding: 30px 15px;
      }
    }

    &__header {
      min-height: 60px;
      display: flex;
      align-items: stretch;
      justify-content: space-between;
      gap: 20px;

      padding: 20px 80px 20px 30px;
      box-shadow: inset 0px 1px 0 0 rgba(255, 255, 255, 0.19);
      background: #151720;
      border-radius: 6px;

      position: relative;
      overflow: hidden;

      color: #fff;

      transition: .3s color,.3s border-radius;

      &:hover{
        cursor: pointer;
      }
      @media screen and (max-width: 640px) {
        padding: 10px 80px 10px 10px;
      }
    }

    &__icon {
      width: 60px;
      height: 100%;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      position: absolute;
      right: 0;
      top: 0;

      box-shadow: inset 0px 1px 0 0 rgba(255, 255, 255, 0.19);
      background-color: #1a1c26;

      transition: .3s background-color;
      &::before {
        content: '›';
        font-size: 28px;
        font-weight: 700;
        color: #fff;

        transform: rotate(90deg);
        transition: .3s transform;
      }
    }
  }
}