.history{
  &-tabs{
    &__nav{
      display: flex;
      align-items: center;
      flex-wrap: wrap;
    }
    &__link{
      display: flex;
      align-items: center;
      padding: 15px 20px;

      font-weight: 700;
      font-size: 15px;
      color: #fff;

      border-radius: 6px;

      transition: .3s color,.3s background-color;
      &.active{
        color: var(--color-accent);
        background-color: #0e0f14;
      }
    }
  }
}