@mixin custom-scrollbar {
    &::-webkit-scrollbar {
      width: 3px;
      height: 3px;
      background-color: #221f31;
      border-radius: 20px;
    }
  
    &::-webkit-scrollbar-thumb {
      background-color: var(--color-accent);
      border-radius: 20px;
    }
  
    &::-webkit-scrollbar-track {
      background-color: #221f31;
      border-radius: 20px;
    }
  }