.aside{
    max-width: 300px;
    width: 100%;
    flex-shrink: 0;
    @media screen and (max-width: 1240px) {
        position: fixed;
        left: 0;
        top: 0;
        height:100vh;
        z-index: 14;

        width: 100vw;
        max-width: 100%;

        background: rgba(#0e0f14,.6);

        opacity: 0;
        visibility: hidden;

        --webkit-backdrop-filter:blur(5px);

        transition: .3s visibility,.5s opacity;
        &--active{
            opacity: 1;
            visibility: visible;
        }
    }
    &-block{
        padding: 20px 20px 20px 30px;
        &--mobile{
            @media screen and (min-width: 1241px) {
                display: none;
            }
        }
        &--tablet{
            @media screen and (min-width: 1701px) {
                display: none;
            }
        }
        & + & {
            border-top: 1px solid #0e0f14;
        }
        &__header{
            padding: 10px 25px;
        }
        &__title{
            font-size: 14px;
            font-weight: 400;
            color: #3e4153;

            line-height: 1.2;
        }
    }
    &-list{
        &__item{
            display: flex;
        }
        &__link{
            display: flex;
            align-items: center;
            gap: 17px;
            width: 100%;

            fill: #fff;
            font-family: var(--main-font);
            font-size: 16px;
            color: #fff;

            padding:20px 25px;
            border-radius: 6px;

            position: relative;

            transition: .3s fill,.3s color,.3s background-color,.3s opacity;
            &::before{
                content: '';
                position: absolute;
                width: 3px;
                height: 22px;
                display: block;

                left: -13px;
                top: calc(50% - 11px);
                border-radius: 4px;
                background: var(--color-accent);
                opacity: 0;
                transition: .3s opacity;
            }
            &:hover{
                opacity: .4;
            }
            &.active,&:hover{
                fill: var(--color-accent);
                color: var(--color-accent);

                background: #1c1d25;
                &::before{
                    opacity: 1;
                }
            }
            .icon{
                width: 18px;
                height: 18px;
                flex-shrink: 0;
            }
        }
    }
    &-footer{
        margin: auto 0 0 0;
    }
    &__wrapper{
        display: flex;
        flex-direction: column;
        gap: var(--regular-gap);

        height: 100%;

        @media screen and (max-width: 1240px) {
            max-width: 300px;
            width: 100%;
            background: #0e0f14;
        }
        @media screen and (max-width: 350px) {
            max-width: 270px;
            width: 100%;
        }
    }
    &__content{
        background: var(--color-block-bg);
        flex-grow: 1;

        overflow: auto;
        border-radius: 6px;

        @include custom-scrollbar();
    }
    &__header{
        padding: 44px 55px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;

        background: #1c1d25 linear-gradient(150deg, rgba(#dca057, .2) 10%, rgba(#1c1d25, .3) 80%);

        @media screen and (max-width: 1440px) {
            padding: 30px;
            height: 150px;
        }
    }
    &__logo{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}