.header{
	position: relative;
	z-index: 12;

	&__burger,&__logo{
		@media screen and (min-width: 1240px) {
			display: none;
		}
	}
	&__logo{
		@media screen and (max-width: 400px) {
			display: none;
		}
	}
	&__wrapper{
		display: grid;
		grid-template-columns: minmax(0,1fr) 400px;
		grid-auto-rows: 100px;
		gap: var(--regular-gap);

		@media screen and (max-width: 1240px) {
			grid-template-columns:300px 67px minmax(0,1fr);
			grid-auto-rows: 60px;
		}
		@media screen and (max-width: 991px) {
			padding: 0 20px;
			grid-template-columns: 100px 67px minmax(0,1fr);
		}
		@media screen and (max-width: 400px) {
			grid-template-columns: 67px minmax(0,1fr);
		}
	}
	&__block{
		background: var(--color-block-bg);
		border-radius: 6px;
		display: flex;
		align-items: center;
		gap: var(--regular-gap);

		padding: 10px 30px 10px 50px;

		@media screen and (max-width: 1700px) {
			justify-content: space-between;
		}

		@media screen and (max-width: 1440px) {
			padding: 20px;
			gap: 10px;
		}

		&--gradient{
			background: #111219 linear-gradient(-150deg, rgba(220, 160, 87, 0.2) 10%, rgba(17,18,25, 0.3) 80%);
		}
	}
	&__logo{
		background: var(--color-block-bg);
		border-radius: 6px;
		display: flex;
		align-items: center;

		padding: 10px;
	}
	&__nav{
		@media screen and (max-width: 1240px) {
			display: none;
		}
	}
	&__info{
		margin: 0 0 0 auto;
		@media screen and (max-width: 1700px) {
			display: none;
		}
	}

	&-notification{
		@media screen and (max-width: 991px) {
			order: 4;
		}
	}

	&-profile{

		&__wrapper{
			display: flex;
			align-items: center;
			gap: 20px;
			@media screen and (max-width: 991px) {
				gap: 10px;
			}
		}
		&__name{
			white-space: nowrap;
			overflow: hidden;
			max-width: 110px;
			width: 100%;
			@media screen and (max-width: 640px) {
				display: none;
			}
			p{
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
		&__img{
			width: 66px;
			height: 66px;
			border-radius: 6px;
			background: rgba(#fff,.3);
			padding: 6px;

			display: flex;
			align-items: center;
			justify-content: center;

			flex-shrink: 0;

			@media screen and (max-width: 1440px) {
				width: 50px;
				height: 50px;

				padding: 3px;
			}

			@media screen and (max-width: 450px) {
				display: none;
			}
			img{
				width: 100%;
				height: 100%;
				border-radius: 6px;
				object-fit: cover;
			}
		}
		&__quit{
			display: flex;
			align-items: center;
			justify-content: center;
			width: 35px;
			height: 35px;
			flex-shrink: 0;
			border-radius: 50%;
			background: rgba(#0e0f14,.2);

			fill:#595b68;

			transition: .3s fill;
			&:hover{
				fill: #fff;
			}
			.icon{
				width: 16px;
				height: 16px;

			}
		}
	}
}

.nav{
	&__list{
		display: flex;
		align-items: center;
		gap: 45px;
	}
	&__item{
		display: flex;
	}
	&__link{
		display: flex;
		align-items: center;
		font-size: 16px;
		color: #fff;

		transition: .3s color;
		&:hover{
			color: var(--color-accent);
		}
	}
}

.info{
	&__wrapper{
		display: flex;
		align-items: flex-end;
		gap: 30px;
		text-align: center;

		@media screen and (max-width: 1800px) {
			gap: 10px;
		}
		&--column{
			flex-direction: column;
			align-items: flex-start;
			text-align: left;
		}
	}
	&__block{
		display: flex;
		flex-direction: column;
		gap: 15px;
		&--row{
			display: grid;
			grid-template-columns: 2fr 3fr;
			width: 100%;
		}
	}
}