:root {
    --main-font: "Inter", sans-serif;
    --regular-gap: 20px;
    --regular-padding:20px;

    --color-block-bg:#111219;
    --color-accent:#dca057;
    --color-accent-light:#ce9652;
    --color-card-bg:#151720;
    @media screen and (max-width:991px) {
        gap: 15px;
    }
}

html{
    scroll-behavior: smooth;
}

body {
    font-family: var(--main-font);
    background: #0e0f14;
    font-weight: 400;
    font-size: 16px;
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;

    &__img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.text{
    &--small{
        font-size: 14px;
        font-weight: 400;
        line-height: 1.2;
    }
    &--base{
        font-weight: 400;
        font-size: 16px;
        line-height: 1.2;
    }
    &--bold{
        font-weight: 700;
    }
    &--big{
        font-size: 20px;
        font-weight: 700;
    }
    &--accent{
        color: var(--color-accent);
    }
    &--white{
        color: #fff;
    }
    &--green{
        color:#2fb257;
    }
    &--red{
        color: #ed3356;
    }
    &--grey{
        color: #424659;
        &--light{
            color: #a69995;
        }
    }

    &--center{
        text-align: center;
    }
}

.title{
    &--small{
        font-size: 23px;
        font-weight: 700;
    }
    &--base{
        font-size: 32px;
        line-height: 1.2;
        font-weight: 800;

        @media screen and (max-width: 991px) {
            font-size: 26px;
        }
    }
    &--big{
        font-size: 44px;
        line-height: 1.2;
        font-weight: 800;
        @media screen and (max-width: 991px) {
            font-size: 32px;
        }
    }
    &--regular{
        font-weight: 400;
    }
    &--bold{
        font-weight: 700;
    }
    &--white{
        color: #fff;
    }
    &--accent{
        color: var(--color-accent);
    }
    &--uppercase{
        text-transform: uppercase;
    }
}

.icon {
    width: 100%;
    height: 100%;
}

.container {
    max-width: 1470px;
    width: 100%;
    margin: 0 auto;

    padding: 0 15px;

    &--big {
        max-width: 1920px;
        padding: 0 100px;

        @media screen and (max-width:1700px) {
            max-width: 1470px;
            padding: 0 15px;
        }
    }
}


.page-wrapper {

    display: flex;
    flex-direction: column;
    overflow: hidden;
    @media screen and (max-width: 991px) {
        height: auto;
        min-height: 100vh;
        overflow: unset;
    }

    &__login{
        min-height: 100vh;
        height: auto;
        overflow: unset;
    }
}

.content {
    display: flex;
    align-items: stretch;
    gap: var(--regular-gap);
    flex-grow: 1;
    max-height: 100%;
    padding: var(--regular-padding);

    height: 100vh;
    min-height: 550px;
    @media screen and (max-width:991px) {
        flex-direction: column;
        min-height: 100vh;
        height: auto;

        padding: 20px 0;
    }

    &__wrapper {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        gap: var(--regular-gap);
    }

    &__block{
        background: var(--color-block-bg);
        border-radius: 6px;
        padding: var(--regular-padding);
        flex-grow: 1;

        overflow: auto;
        @include custom-scrollbar();

        &--padding{
            &--big{
                padding: 50px;
                @media screen and (max-width: 991px) {
                    padding: 20px;
                }
            }
            &--none{
                padding: 0;
            }
        }
    }
}

.block{
    &__wrapper{
        display: flex;
        flex-direction: column;
        gap: 36px;
        &--gap{
            &--none{
                gap: 0;
            }
        }
    }
    &__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        @media screen and (max-width:991px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__title{
        font-size: 23px;
        font-weight: 700;
        color: #fff;

        display: flex;
        align-items: center;
        gap: 10px;
        fill: var(--color-accent);
        .icon{
            width: 26px;
            height: 26px;
        }
    }
    &__item{
        padding: 30px;
        @media screen and (max-width: 991px) {
            padding: 20px;
        }
        & + &{
            border-top: 3px solid #0e0f14;
        }
    }
}

.burger {
    width: 27px;
    height: 20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        cursor: pointer;
    }
    &__item {
        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 3px;

        background: var(--color-accent);
        border-radius: 6px;

        &:nth-child(1) {
            transition:
                0.3s transform,
                0.3s top,
                0.3s background-color;
        }

        &:nth-child(2) {
            top: calc(50% - 1.5px);

            transition:
                0.3s left,
                0.3s opacity,
                0.5s visibility,
                0.3s background-color;
        }

        &:nth-child(3) {
            top: calc(100% - 3px);

            transition:
                0.3s transform,
                0.3s top,
                0.3s background-color;
        }
    }

    &--active {
        .burger__item {
            top: 8px;

            &:nth-child(1) {
                transform: rotate(45deg);
            }

            &:nth-child(2) {
                left: -10px;
                opacity: 0;
                visibility: hidden;
            }

            &:nth-child(3) {
                transform: rotate(-45deg);
            }
        }
    }
}

.button{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    font-family: var(--main-font);
    width: max-content;
    max-width: 100%;
    &:hover{
        cursor: pointer;
    }
    &--base{
        padding: 25px 30px;
        font-size: 16px;
        font-weight: 800;
        line-height: 1;
        gap: 30px;

        border-radius: 6px;

        text-transform: uppercase;
        &--regular{
            text-transform: unset;
        }
        &--full{
            padding: 20px 10px;
            border-radius: 16px;
        }
        &--small{
            padding: 20px;
        }

        @media screen and (max-width: 640px) {
            padding:20px 10px;
            gap: 10px;

            width:100%;
        }
    }
    &--transparent{
        box-shadow: inset 3px 0px 0 0 var(--color-accent);
        border: solid 1px var(--color-accent);
        color: var(--color-accent);

        transition: .3s background-color,.3s color;
        &--bottom{
            box-shadow: inset 0px -3px 0 0 #dca057;
        }
        &:hover{
            background: var(--color-accent);
            color: #111219;
        }
    }
    &--accent{
        border: solid 1px #dca057;
        background-color: rgba(181, 132, 73, 0.1);
        color:#fff;

        fill: var(--color-accent);
        transition: .3s background-color,.3s color,.3s fill;
        &:hover{
            background-color: rgba(181, 132, 73, 1);
        }
        &--color{
            color: var(--color-accent);
            &:hover{
                color: #fff;
                fill: #fff;
            }
        }
        &--fill{
            color: #111219;
            background:#dca057;
        }

    }
    &--arrow{
        position: relative;
        &::before{
            content: '';
            width: 0;
            height: 0;
            position: absolute;
            left: 3px;
            top: calc(50% - 5px);
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;

            border-left: 5px solid var(--color-accent);
        }
    }
    &--full{
        width: 100%;
    }
    &__icon{
        width: 20px;
        height: 20px;
    }
}

.select {
    position: relative;
    z-index: 1;
    &--active {
        z-index: 10;
    }

    &--active &__content--hidden{
        opacity: 1;
        visibility: visible;
    }

    &--active &__label{
        border-radius: 6px 6px 0 0;
        border-color: rgba(#fff, .05);

        &::after {
            transform: rotate(180deg);
        }
    }

    &--full {
        max-width: 100%;
        width: 100%;
    }

    &__tag {
        display: none;
    }

    &__wrapper {
        position: relative;
        width: 100%;
    }

    &__label{
        box-shadow: inset 0px 2px 0 0 #1c1f29;
        background-color: #191b25;

        padding: 10px 20px;
        font-size: 16px;
        font-weight: 700;
        border-radius: 6px;
        color: #fff;

        display: flex;
        align-items: center;
        justify-content: space-between;

        height: 54px;

        &:hover{
            cursor: pointer;
        }
    }

    &__name{
        text-overflow: ellipsis;
        overflow: hidden;
    }

    &__item {
        border-radius: 0;
        width: 100%;
        transition: .3s background-color;

        font-size: 15px;
        line-height: 1;
        color: #fff;

        padding:10px 20px;
        height: 40px;
        &:not(:last-child) {
            border-bottom: 1px solid #1c1f29;
        }

        span{
            text-overflow: ellipsis;
            overflow: hidden;
        }

        &:hover{
            cursor: pointer;
            color: #dca057;
        }

        &--hidden{
            display: none;
        }
    }

    &__label {

        display: flex;
        align-items: center;
        gap: 20px;
        transition: .3s color;
        position: relative;
        z-index: 4;
        &:hover{
            opacity: .8;
        }
        &::after {
            content: '';
            width: 0;
            height: 0;
            display: block;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;

            border-top: 5px solid #4c4f5f;
            transition: .3s transform;
        }
    }

    &__content {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;

        border-radius: 0 0 6px 6px;

        display: flex;
        flex-direction: column;
        transition: .3s opacity, .1s visibility;

        box-shadow: 0px 16px 38px 0 rgba(14, 15, 22, 0.52);
        border: solid 1px #1c1f29;
        border-top: none;

        background-color: #151720;

        max-height: 150px;
        overflow: auto;
        @include custom-scrollbar();
        &--hidden {
            opacity: 0;
            visibility: hidden;
        }
    }
}

.grid{
    display: grid;
    grid-template-columns: minmax(0,1fr);
    gap: var(--regular-gap);

    &__row{
        display: grid;
        grid-template-columns: repeat(12,minmax(0,1fr));
        grid-auto-rows: 1fr;
        gap: var(--regular-gap);

        @media screen and (max-width: 991px) {
            grid-auto-rows: auto;
        }
    }
    &__block{
        grid-column-end: span 4;
        &--middle{
            grid-column-end: span 6;
        }
        &--full{
            grid-column: 1/-1;
            &--tablet{
                @media screen and (max-width: 1500px) {
                    grid-column: 1/-1;
                }
            }
        }
        &--big{
            grid-column-end: span 8;
        }

        @media screen and (max-width: 1100px) {
            grid-column: 1/-1;
        }
    }
}

.status{
    display: flex;
    align-items: center;
    gap: 10px;

    font-size: 16px;
    font-weight: 400;
    --current-color:#ea2243;
    white-space: nowrap;
    &::before{
        content: '';
        width: 5px;
        height: 5px;
        display: block;
        flex-shrink: 0;

        border-radius: 50%;
        background: var(--current-color);
        box-shadow:0 0 10px var(--current-color);
    }
    &::after{
        content: 'Не активен';
        color: var(--current-color);
    }
    &--active{
        --current-color:#2dbb62;
        &::after{
            content: 'Активен';
        }
    }

}

.card{
    &__header{
        width: 100%;
    }
    &__wrapper{
        display: flex;
        align-items: stretch;
        justify-content: space-between;
        gap: var(--regular-gap);
        height: 100%;

        padding: var(--regular-padding);
        border-radius: 6px;
        background: var(--color-card-bg);

        overflow: hidden;
        position: relative;

        &--column{
            flex-direction: column;
        }
        &--passport{
            &::before{
                content: '';
                width: 115px;
                height: 164px;
                display: block;

                position: absolute;
                bottom: -10px;
                right: 20px;

                background: url("../img/passport.png") center center/contain no-repeat;
            }
        }
        &--selfie{
            &::before{
                content: '';
                width: 165px;
                height: 100px;
                display: block;

                position: absolute;
                bottom: 0;
                right: -30px;

                background: url("../img/selfie.png") center center/contain no-repeat;
            }
        }
        &--wallet{
            &::before{
                content: '';
                width: 400px;
                height: 400px;
                display: block;

                position: absolute;
                left: -100px;
                z-index: 1;
                bottom: -200px;
                background:radial-gradient(circle,rgba(#ce9652,.15) 25%, var(--color-card-bg) 70%);
            }
            &::after{
                content: '';
                width: 300px;
                height: 300px;
                display: block;

                position: absolute;
                z-index: 2;

                left: -50px;
                bottom: -120px;

                background: url("../img/wallet.png") center center/contain no-repeat;
            }
        }
        &--contract{
            background: url("../img/contract.jpg") center center/cover no-repeat;
            &--all{
                &::before{
                    content: '';
                    width: 100%;
                    height: 100%;
                    display: block;

                    position: absolute;
                    left: 0;
                    z-index: 1;
                    top: 0;
                    background: linear-gradient(142deg,rgba(206,150,82,0.4) 5%, rgba(206,150,82,0.1) 30%, rgba(206,150,82,0) 60%);

                }
            }
        }
        &--big{
            padding: 30px;

            @media screen and (max-width: 991px) {
                padding: 20px;
            }
        }
        &--icon{
            padding-right: 50px;
            @media screen and (max-width: 991px) {
                padding-right: 20px;
            }
        }
    }
    &__header{
        display: flex;
        flex-direction: column;
        gap: var(--regular-gap);
        &--row{
            align-items: center;
            flex-direction: row;
            gap: 10px;
            justify-content: space-between;
        }
    }
    &__box{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: var(--regular-gap);

        position: relative;
        z-index: 3;

        width: 100%;
        &--small{
            max-width: 455px;
        }
    }
    &__icon{
        width: 55px;
        height: 55px;
        flex-shrink: 0;

        display: flex;
        align-items: center;
        justify-content: center;

        align-self: center;
        position: relative;
        &--shadow{
            &::before{
                content: '';
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                display: block;
                z-index: 1;
                background:rgba(#ce9652,.1);
                border-radius: 50%;

                box-shadow: 0 0 50px rgba(#ce9652, .3);
            }
        }
        &--accent{
            fill: var(--color-accent-light);
        }

        .icon{
            position: relative;
            z-index: 2;
        }
    }
}

.contract{
    &-tag{
        &__wrapper{
            height: 100%;

            padding: 16px 20px;
            border: solid 1px var(--color-accent);
            border-radius: 6px;

            background: rgba(#151720,.55);
        }
    }
    &-tabs{
        display: grid;
        grid-template-columns: 150px minmax(0,1fr);
        @media screen and (max-width: 991px) {
            grid-template-columns: minmax(0,1fr);
        }
        &__content{
            padding: 30px 40px;
            border-radius: 6px;
            background: var(--color-card-bg);

            @media screen and (max-width: 991px) {
                padding: 20px;
            }
        }
        &__nav{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        &__link{
            display: flex;
            align-items: center;
            width: 100%;
            height: 50px;

            font-family: var(--main-font);
            font-size: 16px;
            color: #fff;
            padding: 10px 25px;
            border-radius: 6px 0 0 6px;
            border-left: 2px solid transparent;

            transition: .3s border-color,.3s background-color,.3s color;
            @media screen and (max-width: 991px) {
                border-radius: 6px;
            }
            &.active{
                color: var(--color-accent);
                border-color: var(--color-accent);
                background: var(--color-card-bg) linear-gradient(90deg,rgba(#ce9652,.2), var(--color-card-bg));
            }
            &:not(:last-child){
                border-bottom: 1px solid #151720;
            }
        }
    }

    &-info{
        display: flex;
        flex-direction: column;
        gap: 20px;
        &__header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
        }
        &__title{
            display: flex;
            flex-direction: column;

            gap: 5px;
        }
        &__row{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;

            padding: 10px 0;
            &:not(:last-child){
                border-bottom: 1px solid #1e212e;
            }
        }
    }
}

.login{
    padding: 0;
    &__wrapper{
        position: relative;

        padding: 125px 20px 20px 20px;

        @media screen and (max-width: 640px) {
            padding: 20px;
        }

    }
    &__container{
        max-width: 515px;
        width: 100%;
        margin: 0 auto;
        height: 100%;

        position: relative;
        z-index: 3;

        display: flex;
        flex-direction: column;
        gap: 40px;
        &--big{
            max-width: 640px;
        }
        @media screen and (max-width: 991px) {
            gap: 20px;
        }
    }
    &__header{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 55px;
        @media screen and (max-width: 991px) {
            gap: 30px;
        }
    }
    &__title{
        font-size: 28px;
        line-height: 1.2;
        font-weight: 800;
        @media screen and (max-width: 640px) {
            font-size: 24px;
        }
    }
    &__desc{
        display: flex;
        flex-direction: column;
        gap: 20px;
        text-align: center;
        @media screen and (max-width: 640px) {
            gap: 10px;
        }
    }
    &__footer{
        margin: auto auto 0 auto;

        max-width: 515px;
        width: 100%;
    }
    &__text{
        text-align: center;
        font-size: 13px;
        line-height: 1.2;
        color:#83858e;
    }
    &__logo{
        width: 165px;
        height: 124px;

        @media screen and (max-width: 640px) {
            height: 75px;
            width: 100%;
        }
    }
}

.form{
    &-notice{
        &__wrapper{
            padding: 50px 20px;
            border: solid 1px #dca057;
            background-color: rgba(181, 132, 73, 0.1);
            border-radius: 6px;

            @media screen and (max-width:640px) {
                padding: 30px 20px;
            }

            &--dark{
                background: #111219;
                border: transparent;
            }
        }
        &__text{
            text-align: center;
            font-size: 18px;
            font-weight: 400;
            color: #fff;
            line-height: 1.2;
            @media screen and (max-width:640px) {
                font-size: 16px;
            }
            span{
                color: var(--color-accent);
            }
            &--bold{
                font-weight: 700;
            }
            &--underline{
                text-decoration: underline;
            }
        }
    }
    &-checkbox{
        display: flex;
        align-items: center;
        gap: 10px;
        &:hover{
            cursor: pointer;
        }
        &__item{
            display: none;
        }
        &__item:checked ~ &__checkmark{
            &::before{
                opacity: 1;
            }
        }
        &__checkmark{
            background-color: #13141b;
            width: 20px;
            height: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 6px;

            &::before{
                content: '';
                width: 7px;
                height: 7px;
                display: block;
                flex-shrink: 0;
                border-radius: 50%;
                background: var(--color-accent);


                opacity: 0;
                transition: .3s opacity;
            }
        }
    }
    &-input{
        display: flex;
        flex-direction: column;
        gap: 15px;
        &--row{
            flex-direction: row;
            align-items: center;
        }
        &--small{
            gap: 10px;
        }
        &__label{
            font-size: 14px;
            line-height: 1.2;
            color: #75767d;
        }
        &__item{
            height: 50px;
            border-radius: 12px;
            background-color: #13141b;
            border: solid 1px #20222a;

            padding: 10px 15px;
            color: #fff;
            font-size: 16px;
            font-weight: 400;
            &--radius{
                &--small{
                    border-radius: 6px;
                }
            }
        }
    }
    &-file{
        display: flex;
        align-items: center;
        gap: 30px;
        &__item{
            display: none;
        }
        &__item:valid ~ &__checkmark{
            &::after{
                content: 'загрузить заново';
            }
        }
        &__item:valid ~ &__success{
            display: flex;
        }
        &__success{
            display: none;
            align-items: center;
            font-size: 14px;
            font-weight: 800;
            text-transform: uppercase;
            color:#29ac3f;
            gap: 10px;

            text-align: center;
            .icon{
                width: 20px;
                height: 20px;
                fill: #29ac3f;
            }
        }
        &__checkmark{
            display: flex;
            align-items: center;
            gap: 20px;

            padding: 10px 20px;
            height: 54px;
            border: solid 1px #dca057;
            border-radius: 6px;
            text-align: center;
            width: max-content;
            .icon{
                content: '';
                width: 20px;
                height: 20px;
                display: block;
                flex-shrink: 0;

                fill: var(--color-accent);
            }
            &::after{
                content: 'прикрепить фото';
                font-size: 14px;
                font-weight: 800;
                text-transform: uppercase;
                color: var(--color-accent);
            }
        }
    }
    &__row{
        display: flex;
        align-items: center;
        gap: 20px;
        justify-content: space-between;
        @media screen and (max-width: 640px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__wrapper{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;

        width: 100%;
    }
    &__footer{
        display: flex;
        flex-direction: column;
        gap: 30px;
    }
    &__content{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    &__title{
        font-size: 18px;
        font-weight: 700;
        color: #fff;
    }
}

.verification{
    counter-reset: step;
    &-item{
        counter-increment: step;
        justify-content: space-between;
        height: 100%;
    }
    &-button{
        padding: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media screen and (max-width: 991px) {
            padding: 15px 0;
        }
    }
    &-title{
        &::before{
            content: counter(step)"." ;
            font-size: 23px;
            font-weight: 800;
            color: var(--color-accent);
            line-height: 1.2;
        }
    }
}

.tabs{
    &__content{
            display: none;
            &.active{
                display: block;
            }
    }
}

.table{
    overflow: auto;
    @include custom-scrollbar();
    &__row{
        display: grid;
        grid-template-columns: repeat(4,minmax(0,1fr));
        border-radius: 6px;
        min-width: 600px;
        &:nth-child(even){
            background-color: #151720;
        }
    }
    &__column{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;

        padding: 20px;
        white-space: nowrap;

        p{
            text-overflow: ellipsis;
            overflow: hidden;
        }
        &:first-child{
            justify-content: flex-start;
            text-align: left;
        }
        &:last-child{
            justify-content: flex-end;
            text-align: right;
        }
    }
}

.notification{
    position: relative;
    &--active &-content{
        opacity: 1;
        visibility: visible;
    }
    &--active &-link{
        fill: var(--color-accent);
    }
    &-link{
        fill: #fff;

        display: flex;
        align-items: center;
        justify-content: center;

        width: 18px;
        height: 18px;
        &__icon{
            width: 100%;
            height: 100%;
        }
    }
    &-content{
        padding: 16px 15px 16px 25px;
        background: rgba(#111219,.95);
        border-radius: 20px;
        width: 350px;

        position: absolute;
        top: 0;
        right: calc(100% + 10px);
        box-shadow: 0px 14px 43px 0px rgba(25, 23, 37, 0.92);

        opacity: 0;
        visibility: hidden;

        transition: .3s visibility,.5s opacity;

        @media screen and (max-width:640px) {
            right: 0;
            top: calc(100% + 10px);
            width: 280px;
        }
        &__list{
            @include custom-scrollbar;
            max-height: 260px;
            height: 100%;

            overflow: auto;
            padding-right: 10px;
        }
    }
    &-item{
        padding: 16px 0;
        &:first-child{
            padding-top: 0;
        }
        &__wrapper{
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
        &__header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
        }
        &__time{
            white-space: nowrap;
            flex-shrink: 0;
            font-size: 12px;
            color:#424659;
            font-weight: 700;
        }
        &__title{
            font-size: 14px;
            font-weight: 700;
            color:var(--color-accent);
        }
        &__text{
            font-size: 13px;
            color:#fff;
            font-weight: 500;
        }
    }
    &__button{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 40px;
        border: 1px solid #37334c;
        border-radius: 20px;
        background: rgba(#1e1b2b,.26);

        font-size: 13px;
        text-transform: uppercase;
        color: #fff;
        font-weight: 800;
    }
}

.language-select{
    position: relative;

    &--active &__content{
        visibility: visible;
        opacity: 1;
    }
    &--active &__current{
        border-radius: 6px 6px 0 0;
        &::after{
            transform: rotate(180deg);
        }
    }
    &__content{
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;

        border-radius: 0 0 6px 6px;
        overflow: hidden;

        opacity: 0;
        visibility: hidden;

        transition: .3s visibility,.5s opacity;
    }


    &__current{
        padding: 6px 6px 6px 9px;
        border-radius: 6px;

        transition: .3s border-radius;
        &::after {
            content: '';
            width: 0;
            height: 0;
            display: block;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;

            border-top: 5px solid #4c4f5f;
            transition: .3s transform;
        }
    }
    &__item{
        padding: 6px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;

        background: rgba(#000,.2);

        transition: .3s background-color;
        &:hover{
            background: rgba(#000,.4);
        }
    }
    &__icon{
        width: 25px;
        height: 17px;
        object-fit: cover;
    }
}