.about{
  &__wrapper{
    display: flex;
    flex-direction: column;
    gap: 60px;
    @media screen and (max-width: 991px) {
      gap: 30px;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  &-banner{
    background: var(--color-card-bg);
    border-radius: 6px;

    overflow: hidden;
    position: relative;
    &::after{
      content: '';
      z-index: 2;
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      top: 0;
      display: block;
      background: var(--color-card-bg) linear-gradient(-150deg, rgba(220, 160, 87, 0.2) 5%, rgba(28, 29, 37, 0) 50%);
    }
    &::before{
      content: '';
      position: absolute;
      width: 228px;
      height: 190px;
      display: block;
      bottom: 0;
      right: 0;
      z-index: 3;
      opacity: .3;

      background: url("../img/about.webp") bottom right/contain no-repeat;
    }
    &__wrapper{
      padding: 40px;

      position: relative;
      z-index: 4;
      display: flex;
      flex-direction: column;
      gap: 40px;
      @media screen and (max-width: 991px) {
        padding: 20px;
        gap: 20px;
      }
    }
    &__footer{
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 30px;
      @media screen and (max-width: 1100px) {
        flex-wrap: wrap;
        gap: 15px;
      }
    }
    &__button{
      @media screen and (max-width: 1100px) {
        width: 100%;
      }
    }
  }
}